<template>
  <div class="SocialAlbumWidget">
    <div v-if="isModalVisible && media[activeIndex]">
      <LightBox
        v-if="media[activeIndex].source === 'instagram' || media[activeIndex].source === 'import' || media[activeIndex].source === 'onsite_upload'"
        :image="getImage()"
        :video="media[activeIndex].standard_resolution_url"
        :caption="media[activeIndex].post.content"
        :clicked="clicked"
        :date="media[activeIndex].post.created_time"
        :user="media[activeIndex].post.username"
        @close="closeModal"
        @change="handleChange"
        @click="isModalVisible = !isModalVisible"
      />
      <LightBox
        v-if="media[activeIndex].source === 'review'"
        :image="getImage()"
        :video="media[activeIndex].standard_resolution_url"
        :caption="media[activeIndex].review.content"
        :clicked="clicked"
        :date="media[activeIndex].review.created_at"
        :user="media[activeIndex].review.user.display_name"
        @close="closeModal"
        @change="handleChange"
      />
    </div>
    <div class="SocialAlbumWidget-grid">
      <div
        v-for="(item, index) in media"
        :key="index"
        class="SocialAlbumWidget-item"
      >
        <div
          v-if="item.media_type === 'image'"
          class="SocialAlbumWidget-imageContainer"
          @click="activateItem($event, index)"
        >
          <div class="SocialAlbumWidget-imageOverlay">
            <img
              src="data:image/svg+xml;base64,"
              alt=""
            >
          </div>
          <img
            v-if="useHighResImages"
            class="SocialAlbumWidget-image"
            :src="item.medium_image_url"
            alt=""
          >
          <img
            v-else
            class="SocialAlbumWidget-image"
            :src="item.low_resolution_image_url"
            alt=""
          >
        </div>
        <div
          v-else-if="item.media_type === 'video'"
          class="SocialAlbumWidget-imageContainer"
          @click="activateItem($event, index)"
        >
          <div class="SocialAlbumWidget-imageOverlay">
            <img
              src="data:image/svg+xml;base64,"
              alt=""
            >
          </div>
          <video
            class="SocialAlbumWidget-image"
            :src="item.standard_resolution_url"
            alt=""
          />
        </div>
      </div>
    </div>
    <div class="SocialAlbumWidget-controls">
      <Button
        v-if="allowLoadMore && morePagesAvailable"
        :disabled="clicked"
        @click="loadMore"
      >
        Load More
      </Button>
    </div>
  </div>
</template>

<script>
import Button from './components/Button'
import LightBox from './components/LightBox/LightBox'
import albumApi from './utils/albumApi'
import scrollLock from './utils/scrollLock'

export default {
  name: 'SocialAlbumWidget',
  components: {
    Button,
    LightBox
  },
  props: {
    appKey: {
      type: String,
      default: '1s44JJfzJC1tbUFS9bwKEOS3gOchSTADX3dzqvux'
    },
    albumName: {
      type: String,
      default: 'og-home_page'
    },
    perPage: {
      type: Number,
      default: 9
    },
    allowLoadMore: {
      type: Boolean,
      default: true
    },
    useHighResImages: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      activeIndex: null,
      isModalVisible: false,
      media: [],
      page: 1,
      morePagesAvailable: false,
      clicked: false
    }
  },
  // Needed to lock scroll on open LightBox
  watch: {
    isModalVisible: value => {
      return value === true
        ? scrollLock(true)
        : scrollLock(false)
    }
  },
  // computed: {
  //   orderedMedia () {
  //     const imageTags = this.media.tagged_products.name
  //     var newMedia = []
  //     newMedia.push(this.imageTags)
  //     console.log(this.newMedia)
  //   }
  // },
  created () {
    const matchMobile = window.matchMedia('(max-width: 767px)')
    this.perPage = matchMobile.matches ? 5 : 9
  },
  mounted () {
    this.fetchAlbumData()
  },
  methods: {
    async fetchAlbumData () {
      const keyList = {
        'app_key': this.$props.appKey
      }
      const options = {
        'per_page': this.$props.perPage,
        'page': this.page,
        'album_name': this.$props.albumName
      }

      const { images, pagination } = await albumApi.getData(keyList, options)
      this.media = this.media.concat(images)
      this.morePagesAvailable = (pagination.page * pagination.per_page) < pagination.total
    },
    // Opening LightBox on media item clicked
    activateItem (event, index) {
      this.activeIndex = index
      this.showModal()
    },
    showModal () {
      this.isModalVisible = true
    },
    closeModal () {
      this.isModalVisible = false
      this.activeIndex = null
    },
    /**
     * Adjust the index of the active media item.
     *
     * @param  {Number} value This should be either 1 or -1.
     * @return {void}
     */
    handleChange (value) {
      if (typeof value === 'number' && !isNaN(value)) {
        const newIndex = this.activeIndex + value
        if (newIndex > this.media.length - 1) {
          this.loadMore(() => {
            this.activeIndex = this.activeIndex + 1
          })
        } else if (newIndex < 0) {
          this.activeIndex = this.media.length - 1
        } else {
          this.activeIndex = this.activeIndex + value
        }
      }
    },
    // Load More Button function
    loadMore (done) {
      if (this.morePagesAvailable) {
        this.clicked = true
        this.page += 1
        this.fetchAlbumData().then(resp => {
          this.clicked = false
          done()
        })
      }
    },
    getImage () {
      const matchDesktop = window.matchMedia('(max-width: 1023px)')
      if (!matchDesktop.matches) {
        return this.media[this.activeIndex].original_image_url
      } else {
        return this.media[this.activeIndex].low_resolution_image_url
      }
    }
  }
}
</script>

<style>
.SocialAlbumWidget {
  display: flex;
  flex-direction: column;
}

.SocialAlbumWidget-grid {
  display: grid;
  grid-gap: 0.5rem 0.5rem;
  grid-template-columns: auto;
}

@media screen and (min-width: 768px) {
  .SocialAlbumWidget-grid {
    grid-gap: 1rem 1rem;
    grid-template-columns: repeat(5, 1fr);

  }
}

.SocialAlbumWidget-item:nth-child(9n+1),
.SocialAlbumWidget-item:nth-child(9n+6) {
  grid-column: span 2;
  grid-row: span 2;
}

/* For mobile grid layout */
@media screen and (max-width: 768px) {
  .SocialAlbumWidget-item:nth-child(5n) {
    grid-column: span 1;
    grid-row: span 1;
  }
}

.SocialAlbumWidget-imageContainer {
  height: 0;
  overflow: hidden;
  padding-top: 100%;
  position: relative;
}

.SocialAlbumWidget-image {
  left: 50%;
  min-height: 100%;
  min-width: 100%;
  position: absolute;
  top: 50%;
  transform: translate(-50%,-50%);
  width: 0;
}

.SocialAlbumWidget-imageOverlay {
  align-items: center;
  background: rgba(87,0,255, 0.75);
  bottom: 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: opacity 200ms cubic-bezier(.2, .51, .76, .72);
  z-index: 1;
}

.SocialAlbumWidget-controls {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

/* Hover media query to disallow mobile hover effects */
@media (hover: hover) {
  .SocialAlbumWidget-item:hover .SocialAlbumWidget-imageOverlay {
    opacity: 1;
  }
}

/* IE grid fixes */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .SocialAlbumWidget-grid {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
  }
  .SocialAlbumWidget-item {
    width: 19%;
    margin: 0.5%;
  }
}
</style>
