<template>
  <button
    :class="getClasses()"
    @click="handleClick"
  >
    <Loader v-if="$attrs.disabled" />
    <slot v-else />
  </button>
</template>

<script>
import Loader from './Loader'

export default {
  name: 'Button',
  components: { Loader },
  props: {
    size: {
      type: String,
      default: 'normal',
      validator: value => {
        return ['small', 'normal'].indexOf(value) !== -1
      }
    }
  },
  methods: {
    handleClick (event) {
      this.$emit('click', event)
    },
    getClasses () {
      return {
        // Add different size buttons here
        'sdc-vue-Button': true,
        'sdc-vue-Button--small': this.$props.size === 'small'
      }
    }
  }
}
</script>

<style>
:root {
  --button-transition: 200ms cubic-bezier(.2, .51, .76, .72);
}

.sdc-vue-Button {
  align-items: center;
  background: #5700ff;
  border-radius: 8px;
  border: 2px solid transparent;
  color: #ffffff;
  cursor: pointer;
  display: flex;
  font-family: "Sofia","Helvetica",Arial,sans-serif;
  font-size: 1.125rem;
  justify-content: center;
  letter-spacing: 0.0125rem;
  line-height: 1.375rem;
  padding: 1.25rem 1.875rem;
  touch-action: manipulation;
  user-select: none;
  white-space: nowrap;
  transition: background 200ms cubic-bezier(.2, .51, .76, .72),
              border-color 200ms cubic-bezier(.2, .51, .76, .72),
              color 200ms cubic-bezier(.2, .51, .76, .72);
}

.sdc-vue-Button:disabled {
  padding: 1.175rem 4rem;
}

.sdc-vue-Button--small {
  border-radius: 4px;
  font-size: 0.8125rem;
  line-height: 1;
  padding: 0.625rem 1.75rem;
}

/* Hover media query to disallow mobile hover effects */
@media (hover: hover) {
  .sdc-vue-Button:hover:enabled {
    background: #ffffff;
    border-color: #5700ff;
    color: #5700ff;
  }
}
</style>
