import 'whatwg-fetch'
import queryString from 'query-string'

/**
 * Yotpo app key. This is specific to the "store" in Yotpo.
 *
 * @var  {String}
 *
 * Get Social Data
 * Make an API call to the media service and get all the media data we need.
 *
 * @param   {Object} options API GET params to send.
 * @returns {Promise}        API response in Object format.
 *
 */
const getSocialData = async (keyList, options) => {
  const selectedKey = keyList.app_key
  const params = queryString.stringify({
    ...options
  })

  const response = await window.fetch(`https://api.yotpo.com/v1/widget/${selectedKey}/albums/by_name?${params}`, {
    method: 'GET',
    headers: { 'Content-Type': 'application/json' }
  })
  return response.json()
}

/**
 * Album API
 * Exposes interface for getting data from the specified social media API.
 *
 * @returns {Promise} API response.
 */
const albumApi = {
  async getData (keyList, options) {
    const data = await getSocialData(keyList, options)
    return data.response
  }
}

export default albumApi
