<template>
  <div class="LightBox">
    <div
      class="LightBox-backdrop"
      tabindex="0"
      @click="close"
      @keyup.esc="close"
    >
      <div
        v-touch:swipe.left="next"
        v-touch:swipe.right="previous"
        class="LightBox-content"
        @click.stop
        @keyup="nextItem"
      >
        <!-- Media content section -->
        <div class="LightBox-imageContainer">
          <img
            v-if="image"
            class="LightBox-image"
            :src="image"
            alt=""
          >
          <video
            v-if="video"
            class="LightBox-image"
            alt=""
            controls
            autoplay
          >
            <source
              :src="video"
              type="video/mp4"
            >
          </video>
          <!-- LightBox arrows -->
          <i
            class="LightBox-arrow LightBox-arrow--left"
            @click="previous"
          />
          <div
            v-if="clicked"
            class="LightBox-loading"
          >
            <Loader />
          </div>
          <i
            class="LightBox-arrow LightBox-arrow--right"
            @click="next"
          />
        </div>
        <!-- Non-media section -->
        <div class="LightBox-body">
          <div class="LightBox-copy">
            <div class="LightBox-copyInfo">
              <div class="LightBox-profilePic">
                <div class="LightBox-profilePicLetter">
                  {{ user.substr(0, 1).toUpperCase() }}
                </div>
              </div>
              <div class="LightBox-copyInfoText">
                <div class="LightBox-copyUsername">
                  {{ user.toLowerCase() }}
                </div>
                <div class="LightBox-copySubheader">
                  Verified Buyer
                </div>
              </div>
            </div>
            <div class="LightBox-caption">
              {{ formatCaption(caption) }}
            </div>
            <div class="LightBox-date">
              {{ date.substring(0,10) }}
            </div>
          </div>
          <!-- Call to action area -->
          <div class="LightBox-cta">
            <div class="LightBox-ctaText">
              Get started on a smile you'll love by taking our free smile assessment.
            </div>
            <a
              href="https://smiledirectclub.com/smile_assessment"
              class="LightBox-aLink"
            >
              <Button
                class="LightBox-ctaButton"
                size="small"
              >
                Am I a candidate?
              </Button>
            </a>
          </div>
        </div>
        <button
          class="LightBox-close"
          @click="close"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Button from '../Button'
import Loader from '../Loader'

export default {
  name: 'Modal',
  components: {
    Button,
    Loader
  },
  props: {
    image: {
      type: String,
      required: false,
      default: null
    },
    video: {
      type: String,
      required: false,
      default: null
    },
    caption: {
      type: String,
      required: false,
      default: ''
    },
    date: {
      type: String,
      required: false,
      default: ''
    },
    user: {
      type: String,
      required: false,
      default: ''
    },
    clicked: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data () {
    return {
      open: false
    }
  },
  mounted () {
    document.addEventListener('keyup', this.nextItem)
  },
  methods: {
    close () {
      this.$emit('close')
    },
    // 3 functions below refer to handling indexing though media
    changeItem (value) {
      this.$emit('change', value)
    },
    previous () {
      this.changeItem(-1)
    },
    next () {
      this.changeItem(1)
    },
    // Indexing through lightbox using arrow keys
    nextItem (e) {
      if (e.keyCode === 37) {
        this.changeItem(-1)
      } else if (e.keyCode === 39) {
        this.changeItem(1)
      }
    },
    openModal () {
      this.open = true
    },
    closeModal () {
      this.open = false
    },
    // Don't allow caption to exceed 320 characters
    formatCaption (caption = '') {
      const limit = 320

      return caption.length > limit
        ? `${caption.substr(0, limit)}…`
        : caption
    }
  }
}
</script>

